<template>
  <div class="rigesterPageStyle">
    <el-row class="headRow">
      <span class="titleSpan">{{title}}</span>
      <span class="subtitleSpan">{{subtitle}}</span>
    </el-row>
    <el-steps :active="currentStep" simple>
      <el-step title="填写帐户信息" icon="el-icon-edit"></el-step>
      <el-step title="注册成功" icon="el-icon-success"></el-step>
    </el-steps>
    <div v-if="currentStep === 1">
      <el-row class="titleRow">
        <span class="formTitle">必填信息</span>
      </el-row>
      <el-form ref="ruleForm" status-icon :model="ruleForm" :rules="rules">
        <el-form-item label="用户名" label-width="200px" prop="username">
          <el-input
            class="normalInput"
            placeholder="请输入4-16位英文字母或数字"
            autocomplete="off"
            v-model="ruleForm.username"
          ></el-input>
        </el-form-item>
        <el-form-item label="姓名" label-width="200px" prop="name">
          <el-input
            class="normalInput"
            placeholder="请输入姓名"
            autocomplete="off"
            v-model="ruleForm.name"
          ></el-input>
        </el-form-item>
        <el-form-item label="密码" label-width="200px" prop="password">
          <el-input
            class="normalInput"
            placeholder="请输入6-16位密码"
            autocomplete="off"
            :type="passw"
            v-model="ruleForm.password"
          >
            <i slot="suffix" :class="icon" @click="showPass"></i>
          </el-input>
        </el-form-item>
        <el-form-item label="确认密码" label-width="200px" prop="surePassword">
          <el-input
            class="normalInput"
            placeholder="请确认密码"
            autocomplete="off"
            :type="passw"
            v-model="ruleForm.surePassword"
          >
            <i slot="suffix" :class="icon" @click="showPass"></i>
          </el-input>
        </el-form-item>
      </el-form>
      <el-row class="titleRow">
        <span class="formTitle">选填信息</span>
      </el-row>
      <el-form>
        <el-form-item label="所属部门" label-width="200px">
          <el-input class="normalInput" placeholder="请填写所属部门，例如行政部" v-model="depart"></el-input>
        </el-form-item>
        <el-form-item label="手机号" label-width="200px">
          <el-input class="normalInput" placeholder="请输入手机号" v-model="phone"></el-input>
        </el-form-item>
        <el-form-item label="固定电话" label-width="200px">
          <el-input class="normalInput" placeholder="请输入固定电话" v-model="fixedPhone"></el-input>
        </el-form-item>
        <el-form-item label="电子邮箱" label-width="200px">
          <el-input class="normalInput" placeholder="请填写电子邮箱" v-model="email"></el-input>
        </el-form-item>
        <el-form-item label="验证码" label-width="200px">
          <el-input class="codeInput" placeholder="请填写验证码" prop="authCode" v-model="authCode"></el-input>
          <abbr title="图片看不清？点击重新得到验证码" @click="getSessionId">
            <img id="sessionIdImage" class="codeStyle" type="image" alt="这里将换验证码" :src="url" />
          </abbr>
        </el-form-item>
        <el-row class="buttonRow">
          <el-button type="danger" class="buttonStyle" @click="cancelEvent('ruleForm')">
            <!--此处需要''-->
            取消
          </el-button>
          <el-button type="primary" class="buttonStyle" @click="commitEvent('ruleForm')">提交</el-button>
        </el-row>
      </el-form>
    </div>
    <div v-else>
      <el-row class="finishedRow">
        <img class="imgStyle" src="../../assets/OK.png" />
        <span class="finishedSpan">
          完成注册,点击
          <a class="finishedA" @click="toLogin">这里</a>
          进入
        </span>
      </el-row>
    </div>
  </div>
</template>

<script>
import Request from "@/libs/request.js";
import { SessionStorage } from "wii-fe-utils";
import ElSlPanel from "element-ui/packages/color-picker/src/components/sv-panel";
export default {
  name: "rigester",
  components: { ElSlPanel },
  data() {
    let checkUsername = (rule, value, callback) => {
      let pattern = /[a-z0-9]{4,16}/g;
      if (!value) {
        return callback("用户名不可为空");
      } else if (!pattern.exec(value)) {
        return callback("请输入4-16位英文字母或数字");
      }
      callback();
    };
    let checkName = (rule, value, callback) => {
      if (!value) {
        return callback("姓名不可为空");
      }
      callback();
    };
    let checkPassword = (rule, value, callback) => {
      let pattern = /\w{4,16}/g;
      if (!value) {
        return callback("密码不可为空");
      } else if (!pattern.exec(value)) {
        return callback("字母、数字、下划线组成长度为4-16的密码");
      }
      return callback();
    };
    let checkPasswordAgain = (rule, value, callback) => {
      if (!value) {
        return callback("请输入确认密码");
      } else if (this.ruleForm.surePassword !== this.ruleForm.password) {
        return callback("确认密码必须和密码保持一致");
      }
      return callback();
    };
    let checkAuthCode = (rule, value, callback) => {
      if (!value) {
        return callback("请输入验证码");
      }
      return callback();
    };
    return {
      title: "ATF",
      subtitle: "用户注册",
      currentStep: 1, //默认注册在填写信息栏
      url: "", //验证码地址
      sessionId: "", //验证码
      validatorSession: false,
      passw: "password", //显示密码
      icon: "el-input__icon el-icon-view",
      /*
       * 表单信息
       */
      ruleForm: {
        username: "", //用户名
        name: "", //姓名
        password: "", //密码
        surePassword: "" //确认密码
      },
      depart: "", //所属部门
      phone: "", //手机号
      fixedPhone: "", //固定电话
      email: "", //电子邮箱
      authCode: "", //填写的验证码
      rules: {
        username: [
          {
            validator: checkUsername,
            trigger: "blur"
          }
        ],
        name: [
          {
            validator: checkName,
            trigger: "blur"
          }
        ],
        password: [
          {
            validator: checkPassword,
            trigger: "blur"
          }
        ],
        surePassword: [
          {
            validator: checkPasswordAgain,
            trigger: "blur"
          }
        ],
        authCode: [
          {
            validator: checkAuthCode,
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {
    this.getSessionId();
  },
  mounted() {
    this.getSessionId();
  },
  computed: {},
  methods: {
    getSessionId() {
      let _this = this;
      Request({
        url: "/userController/getSessionId",
        method: "POST",
        params: {}
      })
        .then(res => {
          console.log("验证码获取成功", res);
          sessionStorage.setItem("sessionId", res.obj.sessionId);
          _this.sessionId = sessionStorage.getItem("sessionId");
          _this.url =
            "http://140.143.16.21:8080/atfcloud2.0a/userController/authCode?abc=" +
            Math.random() +
            "&sessionId=" +
            sessionStorage.getItem("sessionId");
          console.log("url");
        })
        .catch(err => {
          console.log("验证码获取失败", err);
        });
    },
    //取消清空表单
    cancelEvent(formName) {
      this.$refs[formName].resetFields();
    },
    //先进行表单验证，然后提交请求
    commitEvent(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let qs = require("qs");
          Request({
            url: "/userController/register",
            method: "POST",
            params: qs.stringify({
              username: this.ruleForm.username,
              reallyname: this.ruleForm.name,
              password: this.ruleForm.password,
              repassword: this.ruleForm.surePassword,
              dept: this.depart,
              tel: this.phone,
              phone: this.fixedPhone,
              email: this.email,
              authCode: this.authCode,
              sessionId: this.sessionId
            })
          })
            .then(res => {
              console.log("注册成功", res);
              this.$message.success("注册成功");
              this.currentStep = 2;
            })
            .catch(err => {
              console.log("注册失败", err);
              this.$message.error(err.msg);
            });
          //用户名验证(存在问题)
          // Request({
          //     url: 'http://10.108.226.152:8080/ATFCloud/userController/checkUser',
          //     method: 'POST',
          //     params: qs.stringify({
          //         username: this.username
          //     })
          // }).then(res => {
          //     console.log('用户名校验通过',res)

          // }).catch(err => {
          //     console.log('用户名校验失败',err)
          // })
        } else {
          this.$message.error("表单验证失败");
        }
      });
    },
    checkAuthCode() {
      let qs = require("qs");
      console.log("authCode:", this.authCode);
      console.log("sessionId:", this.sessionId);
      Request({
        url: "/userController/checkauthcode",
        method: "POST",
        params: qs.stringify({
          authCode: this.authCode,
          sessionId: this.sessionId
        })
      })
        .then(res => {
          console.log("验证码验证通过", res);
          this.validatorSession = true;
        })
        .catch(err => {
          console.log("验证码验证失败", err);
        });
    },
    showPass() {
      if (this.passw === "text") {
        this.passw = "password";
        this.icon = "el-input__icon el-icon-view";
      } else {
        this.passw = "text";
        this.icon = "el-input__icon el-icon-loading";
      }
    },
    toLogin() {
      this.$router.push({ path: "/login" });
    }
  }
};
</script>

<style scoped>
.rigesterPageStyle {
  margin: 15px auto;
  width: 60%;
  height: 100%;
}
/* 顶部样式 */
.headRow {
  margin-top: 30px;
  margin-left: 10px;
  margin-bottom: 30px;
}
.titleSpan {
  color: red;
  font-weight: 700;
  font-family: "Verdana, Geneva, sans-serif";
  font-size: 24px;
}
.subtitleSpan {
  font-family: Verdana, Geneva, sans-serif;
  font-weight: 600;
  font-size: 18px;
  color: #000;
  margin-left: 10px;
}
.titleRow {
  margin-top: 20px;
  margin-bottom: 10px;
}
.formTitle {
  margin-left: 100px;
  font-weight: 500;
  font-size: 18px;
}
.normalInput {
  width: 400px;
}
.codeInput {
  width: 150px;
}
.codeStyle {
  width: 150px;
  height: 40px;
  margin-left: 20px;
  margin-bottom: -15px;
}
.buttonRow {
  margin-left: 200px;
}
.buttonStyle {
  width: 150px;
  margin-left: 10px;
}
.imgStyle {
  width: 60px;
  height: 60px;
  margin-top: 20px;
}
.finishedRow {
  width: fit-content;
  margin: 30px auto auto auto;
}
.finishedSpan {
  font-size: 18px;
}
.finishedA {
  color: rgb(0, 102, 255);
  cursor: pointer;
}
</style>